/* App.css */
.App {
  text-align: center;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden; /* Hide overflow to prevent scrollbars */
}

.black-bar-side {
  width: var(--black-bar-width);
  background-color: black;
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
}

.black-bar-top {
  height: var(--black-bar-top-height);
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.logo-small {
  display: block; /* Ensure the image is treated as a block element */
  max-height: 100%; /* Set the maximum height to 90% of the container */
  max-width: 100%; /* Ensure the image doesn't exceed the container's width */
  margin: 0 auto; /* Center the image horizontally using margin */
}

.black-bar-side.left {
  left: 0;
  display: flex;
  align-items: center;
  padding-left: 10px;
}

.black-bar-side.right {
  right: 0;
  display: flex;
  align-items: center;
  padding-right: 10px;
}

.black-bar-bottom {
  height: var(--black-bar-bottom-height);
  background-color: black;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.menu-button {
  cursor: pointer;
  z-index: 1000;
  color: #FFF;
  font-size: 48px;
  margin-left: 10px;
  position: absolute;
  top: 10px;
  left: 10px;
  width: var(--menu-button-size);
  max-width: calc(var(--black-bar-width) - 10px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.MuiModal-root {
  display: flex; 
  position: fixed; /* Use fixed position to keep the modal in place */
  top: 0; /* Position from the top */
  left: -250px; /* Initially position the modal off-screen to the left */
  animation: slideInModal 0.3s forwards; /* Apply the slide animation */
}

.MuiSvgIcon-root {
  width: 90%;
  height: auto !important; /* Maintain the aspect ratio */
  max-height: 70px !important;
  min-height: 50px !important;
}

/* Define the keyframe animation */
@keyframes slideInModal {
  from {
    left: -250px; /* Start position off-screen to the left */
  }
  to {
    left: 0; /* End position to be fully visible */
  }
}

/* Add a new keyframe animation for sliding out the modal */
@keyframes slideOutModal {
  from {
    left: 0; /* Start position fully visible */
  }
  to {
    left: -250px; /* End position off-screen to the left */
  }
}

.MuiSvgIcon-root {
  width: 100% !important;
}

.modal-content {
  width: 250px;
  background-color: black;
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.3);
  padding: 20px;
  color: white;
  outline: none;
}

.modal-list {
  padding: 0;
}

.MuiTypography-root {
  font-size: 1.5rem !important;
  font-family: 'PlayFair Display SC', serif !important;
  font-family: 'Montserrat Alternates', sans-serif !important;
}

.modal-item {
  color: white;
  padding: 10px;
  transition: background-color 0.3s;
  background-color: black;
}

.modal-item::before {
  content: "";
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-image: url('./img/bean.png'); /* Replace with your image source path */
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 10px; /* Add some space between the image and text */
}

.modal-item:hover {
  background-color: #222 !important;
}

:root {
  --black-bar-bottom-height: 100px;
  --black-bar-width: 5vw;
  --menu-button-size: 50px; /* Set the default size of the menu button */
  --black-bar-top-height: 70px;
}

@media (max-width: 768px) {
  :root {
    --black-bar-width: 10vw;
  }
}


@media (max-width: 400px) {
  :root {
    --black-bar-width: 0;
    --menu-button-size: 40px; /* Adjust the value as needed */
  }
}